import { Injectable } from '@angular/core';
import {HttpClient , HttpHeaders} from "@angular/common/http"
import { Subject, Observable } from 'rxjs';
import { AuthUserService } from '../auth_user/auth-user.service';

@Injectable({
  providedIn: 'root'
})
export class AdminControlService {

  public baseApiUrl = 'http://localhost:5000'
  public actualProduct = new Subject();
  public actualProduct$ = this.actualProduct.asObservable()

  constructor(private http:HttpClient, private authUserService:AuthUserService) { }

  httpOptions = {
    headers: new HttpHeaders({
      'authorization': this.authUserService.getUserDetails().token 
    })
  }

  clickedOnProduct(product:any){
    this.actualProduct.next(product)
  }

  updateProductFromDB(productUpdate:any){
    const result = this.http.post(`/admin/updateProduct`, productUpdate , this.httpOptions).toPromise();
  }

  addProductToDB(new_product:any){
    const result = this.http.post(`/admin/addProduct`, new_product ,this.httpOptions ).toPromise();
  }

}
