import { Component, OnInit } from '@angular/core';
import { ProductsStoreService } from "../../services/products-store/products-store.service"
import { Subscription } from 'rxjs';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { CartActionsService } from 'src/app/services/cart-actions/cart-actions.service';
import { AuthUserService } from 'src/app/services/auth_user/auth-user.service';
import { SocketChannelService } from 'src/app/services/socket-channel/socket-channel.service';

@Component({
  selector: 'app-products-list',
  templateUrl: './products-list.component.html',
  styleUrls: ['./products-list.component.css']
})
export class ProductsListComponent implements OnInit {

  public subscription_products: Subscription;
  public subscription_cart: Subscription;
  //public subscription_admin_update : Subscription;
  public productsList: any =[]
  public customer_cart: any;
  public visible: string = "none"
  public baseImagePath = "../../../assets/images/"


  public closeResult = '';
  public quantity: Number = 1;
  public page:any = 1;
  public pageSize:any = 8
  public current_products:any
 

  constructor(public productsStoreService: ProductsStoreService, private modalService: NgbModal,
    private cartActionService: CartActionsService, private authUserService: AuthUserService,
    private socketService: SocketChannelService) {
    this.subscription_products = this.getSubscriberOfProductsList();
    this.subscription_cart = this.getSubscriberOfCart();

    this.socketService.updateProductByAdmin().subscribe((updatedList) => {
      this.productsList = updatedList;
      this.updating_current_list()
      this.productsStoreService.identifyCategory()
    })

    this.productsStoreService.identifyCategory()
  }

  ngOnInit(): void {
  }

  getSubscriberOfProductsList() {
    return this.productsStoreService.getProducts().subscribe((productsObj) => {
      this.productsList = productsObj;
      this.page = 1
      this.updating_current_list()
      this.productsStoreService.identifyCategory()
    })
  }

  getSubscriberOfCart() {
    return this.cartActionService.getCustomerCart().subscribe((cart) => {
      this.customer_cart = cart;
    })
  }

  open(content: any, id: any) {
    const productExist = this.findInCart(id)
    if (productExist)
      this.quantity = productExist.quantity
    this.modalService.open(content, { centered: true }).result.then((result) => {
      this.quantity = 1;
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return `with: ${reason}`;
    }
  }

  addToCart(product: any) {
    const productDetails = {
      product_key: product.id,
      total_price: this.roundNumber(Number(this.quantity) * Number(product.price)),
      quantity: this.quantity,
      shopping_cart_key: this.authUserService.getUserDetails().customer_cart_id
    }
    this.cartActionService.addProductToCart(productDetails)
  }

  counterUp() {
    this.quantity = Number(this.quantity) + 1
  }

  counterDown() {
    this.quantity = Number(this.quantity) - 1
  }


  findInCart(id: any) {
    return this.customer_cart.find((product: any) => {
      return product.id === id
    })
  }

  updating_current_list(){
    this.current_products = this.productsList.slice(((this.page-1) * this.pageSize) , this.page * this.pageSize)
  }

  roundNumber(number:any){
    return Math.round(number * 100)/100;
  }

  


}
