import { Injectable } from '@angular/core';
import { HttpClient } from "@angular/common/http"
import { Subject, Observable } from 'rxjs';
import { map } from 'rxjs/operators'


@Injectable({
  providedIn: 'root'
})
export class AuthUserService {
  public userConnected: boolean = false
  public currentCase: string = "no-purchase"
  public shoppingButton:string = "Start Shopping"
  public baseApiUrl = 'http://localhost:5000'
  public LogInUser = new Subject();
  public LogInUser$ = this.LogInUser.asObservable()

  constructor(private http: HttpClient) {}

  loginUser(userDetails: any) {
    return this.http.post(`/auth/login`, userDetails).toPromise()
  }

  onUserLogin(user: any) {
    this.LogInUser.next(user)
  }

  registerUser(userDetails: any) {
    return this.http.post(`/auth/register`, userDetails).toPromise()
  }

  isIdValid(id: any) {
    return this.http.post(`/auth/validId`, id).toPromise()
  }

  setUserDetails(user_details: any) {
    localStorage.setItem('user_details', JSON.stringify(user_details))
  }

  getUserDetails(): any {
    return JSON.parse(localStorage.getItem('user_details') || '{}')
  }

  getCartUserInfo(customer_id: any) {
    return this.http.get(`/report/getCartDetails?customer_id=${customer_id}`).toPromise()
  }


}
