import { Component, OnInit } from '@angular/core';
import { AuthUserService } from 'src/app/services/auth_user/auth-user.service';
import { ProductsStoreService } from "../../services/products-store/products-store.service"

@Component({
  selector: 'app-nav-bar-category',
  templateUrl: './nav-bar-category.component.html',
  styleUrls: ['./nav-bar-category.component.css']
})
export class NavBarCategoryComponent implements OnInit {
  public color: string = "grey"
  public categoriesArray: any
  public searchKey: any


  constructor(private productStoreService: ProductsStoreService, public authUserService: AuthUserService) { }

  ngOnInit(): void {
    console.log("ngOninit category")
    this.getCategories()
  }

  async getCategories() {
    await this.productStoreService.getCategoriesFromDB()
    .then(
      (result) => {
        this.categoriesArray = result
        this.productStoreService.categories = result
        this.clickedCategory(1)
      },
      (error) => {
        console.log(error)
        alert("something went wrong...")
      }
    )
  }

  clickedCategory(id: Number) {
    if (!this.productStoreService.getProductsFromLs(id)) {
      this.productStoreService.getProductsCategory(id)
    }

  }


  searchProducts() {
    if (!this.searchKey) return;
    this.productStoreService.searchProduct(this.searchKey)
  }

}
