import { Component, OnInit } from '@angular/core';
import { AuthUserService } from "../../../services/auth_user/auth-user.service"
import { Router } from "@angular/router"
import { Subscription } from 'rxjs';
import { CartActionsService } from 'src/app/services/cart-actions/cart-actions.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  public email: string;
  public password: string;
  public params_message: boolean = false
  public auth_message: boolean = false
  public actionSuccess: boolean = false

  constructor(private router: Router, private authService: AuthUserService, public cartActionservice: CartActionsService) {
    this.email = "";
    this.password = "";
  }

  ngOnInit(): void {}

  async loginProcess() {
    await this.login();
    if (!this.actionSuccess) return;
    //the next actions only users - if you are admin user return;

    this.cartActionservice.initHeaders();
    await this.initCart(this.authService.getUserDetails().id)
  }

  async login() {
    this.auth_message = false;

    if (!this.email || !this.password) {
      this.params_message = true;
      this.actionSuccess = false;
      return;
    }
    this.params_message = false;

    await this.authService.loginUser({ email: this.email, password: this.password }).then(
      (data: any) => {
        if (data.unauthorized) {
          this.auth_message = true;
          this.actionSuccess = false;
          return;
        }

        this.authService.setUserDetails(data)
        this.authService.onUserLogin(data)

        if (data.role) {
          this.authService.userConnected = true;
          this.router.navigate(['/shop_for_admin'])
          this.actionSuccess = false;
          return;
        }

        this.actionSuccess = true;
      },
      (fail: any) => {
        alert("something went wrong...")
      }
    );
  }

  async initCart(id: any) {
    await this.cartActionservice.createShoppingCart(id).then((shopping_cart_id: any) => {
      const userData = this.authService.getUserDetails()
      userData["customer_cart_id"] = shopping_cart_id.id
      this.authService.setUserDetails(userData)
      this.authService.userConnected = true;
    })
  }

  register() {
    this.router.navigate(['/registration'])
  }
}
