import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from "@angular/common/http"
import { AuthUserService } from '../auth_user/auth-user.service';

@Injectable({
  providedIn: 'root'
})
export class OrdersActionsService {

  public baseApiUrl = 'http://localhost:5000/orders'
  public total_price: any
  public order_complete: boolean = false
  public file: any = ""

  constructor(private http: HttpClient, private authUserService: AuthUserService) { }

  httpOptions = {
    headers: new HttpHeaders({
      'authorization': this.authUserService.getUserDetails().token 
    })
  }

  isShippingDateAvailable(date: any) {
    return this.http.get(`/orders/checkShippingDate?shipping_date=${date}` ,this.httpOptions).toPromise()
  }

  completeAction(order_details: any) {
    return this.http.post(`/orders/newOrder`, order_details ,this.httpOptions).toPromise()
  }

  getReceiptLink(userName: any) {
    return this.http.get(`/files?userName=${userName}` ,this.httpOptions).toPromise()
  }

}
