import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AdminPageComponent } from './components/pages/admin-page/admin-page.component';
import { HomePageComponent } from './components/pages/home-page/home-page.component';
import { OrderPageComponent } from './components/pages/order-page/order-page.component';
import {RegistrationComponent} from './components/pages/registration/registration.component'
import { ShoppingPageComponent } from './components/pages/shopping-page/shopping-page.component';


const routes: Routes = [
  {path:"registration" , component: RegistrationComponent},
  {path:"shop" , component: ShoppingPageComponent},
  {path:"shop_for_admin" , component: AdminPageComponent},
  {path:"order_page" , component: OrderPageComponent},
  {path:"**" , component: HomePageComponent}
 
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
