import { Component, OnInit } from '@angular/core';
import { AuthUserService } from "../../../services/auth_user/auth-user.service"
import { Router } from "@angular/router"
import * as moment from 'moment';
import { Subscription } from 'rxjs';
import { LiveReportService } from 'src/app/services/live-report/live-report.service';
import { CartActionsService } from 'src/app/services/cart-actions/cart-actions.service';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html',
  styleUrls: ['./home-page.component.css']
})
export class HomePageComponent implements OnInit {

  public cart_created_time: any = ""
  public cart_total: any = null
  public last_purchase: any = ""
  public subscribeUser: Subscription
  public productStock: any
  public ordersReport: any
  public userData: any


  constructor(public authService: AuthUserService, private router: Router, public liveReportService: LiveReportService,
    public cartActionservice: CartActionsService) {
    this.authService.currentCase = "no-purchase"
    this.authService.shoppingButton = "Start Shopping"

    this.subscribeUser = this.authService.LogInUser.subscribe((info: any) => {
      
      if (Object.entries(info).length !== 0) {
        this.authService.userConnected = true;
        if (info.role === 1) return;
        console.log("home page constructor")
        this.getLiveReport()
        this.userShoppingDetails()
      } else {
        console.log("else - home page constructor")
        this.authService.userConnected = false;
      }

    })
  }

  ngOnInit(): void {
    const userData = this.authService.getUserDetails();
    if (Object.entries(userData).length === 0) {
      this.authService.userConnected = false;
      this.getLiveReport()
      return;
    }
    this.authService.userConnected = true;
    if (!userData.role) {
      this.getLiveReport()
      this.userShoppingDetails()
    } else
      this.router.navigate(['/shop_for_admin'])
  }

  async startShopping() {
    this.router.navigate(['/shop'])
  }

  async getLiveReport() {
    await this.liveReportService.productsInStock().then(
      (info: any) => {
        this.productStock = info.product_stock
      },
      (fail: any) => {
        console.log("fail", fail)
        alert("something went wrong...")
      }
    )

    await this.liveReportService.totalOrders().then(
      (info: any) => {
        this.ordersReport = info.num_of_orders
      },
      (fail: any) => {
        console.log("fail", fail)
        alert("something went wrong...")
      }
    )
  }

  userShoppingDetails() {
    const personalDetails = this.authService.getUserDetails()
    this.authService.getCartUserInfo(personalDetails.id).then((result) => {
      this.caseWelcomePage(result)
    })
  }


  caseWelcomePage(details: any) {
    switch (details.case) {
      case "last-order":
        this.last_purchase = moment(details.last_order_time).format('DD.MM.YYYY, H:mm');
        this.authService.currentCase = details.case
        break;
      case "no-purchase":
        this.authService.currentCase = details.case
        break;
      case "open-cart":
        this.cart_created_time = moment(details.created_time).format('DD.MM.YYYY, H:mm');
        this.cart_total = details.total_price
        this.authService.shoppingButton = "Resume shopping"
        this.authService.currentCase = details.case
        break;
      default:
        this.authService.currentCase = "no-purchase"
        this.authService.shoppingButton = "Start Shopping"
        break;
    }
  }

  roundNumber(number: any) {
    return Math.round(number * 100) / 100;
  }

}
