import { Component, OnInit } from '@angular/core';
import { AuthUserService } from "../../services/auth_user/auth-user.service"
import { Subscription } from "rxjs"
import { Router } from "@angular/router"
import { CartActionsService } from 'src/app/services/cart-actions/cart-actions.service';
@Component({
  selector: 'app-logo-line',
  templateUrl: './logo-line.component.html',
  styleUrls: ['./logo-line.component.css']
})
export class LogoLineComponent implements OnInit {

  public subscription: Subscription;
  public loginUser: any

  constructor(public authUserService: AuthUserService, private router: Router , private cartActionsService : CartActionsService) {
    this.subscription = this.authUserService.LogInUser$.subscribe((loginObj: any) => {
      const { user } = loginObj
      this.loginUser = user || "guest"
    })
  }

  ngOnInit(): void {
    const isUserExist = this.authUserService.getUserDetails()
    if (isUserExist.user)
      this.loginUser = isUserExist.user
    else this.loginUser = "guest"

  }

  logout() {
    const customer_cart_id =  this.authUserService.getUserDetails().customer_cart_id
    if(customer_cart_id)
      this.cartActionsService.checkShopCart(customer_cart_id)

    //localStorage.removeItem('user_details');
    localStorage.clear()
    this.authUserService.onUserLogin({})
    this.authUserService.userConnected = false;
    this.router.navigate(['/'])
  }

}
