import { Component, OnInit } from '@angular/core';
import { Subscription } from 'rxjs'
import { AdminControlService } from 'src/app/services/admin-control/admin-control.service';
import { ProductsStoreService } from 'src/app/services/products-store/products-store.service';
import { SocketChannelService } from 'src/app/services/socket-channel/socket-channel.service';

@Component({
  selector: 'app-admin-sidebar',
  templateUrl: './admin-sidebar.component.html',
  styleUrls: ['./admin-sidebar.component.css']
})
export class AdminSidebarComponent implements OnInit {

  public subscription: Subscription;
  public actual_product: any
  public productOnClicked: boolean = false
  public insertionOnClicked: boolean = false
  public new_product: any
  public insert_error_message: boolean = false
  public update_error_message: boolean = false

  constructor(private adminControlService: AdminControlService, private productsStoreService: ProductsStoreService,
    private socketService: SocketChannelService) {
    this.subscription = this.adminControlService.actualProduct$.subscribe((OnProduct: any) => {
      this.actual_product = {
        id: OnProduct.id, product_name: OnProduct.product_name, category_key: OnProduct.category_key,
        price:  Math.round(OnProduct.price * 100)/100, image: OnProduct.image
      }
      this.productOnClicked = true;
    })
    this.new_product = { "product_name": "", "category_key": null, "price": null, "image": "" }
  }

  ngOnInit(): void {
  }


  update() {
    if (!this.actual_product.id || !this.actual_product.product_name || !this.actual_product.category_key || !this.actual_product.price || !this.actual_product.image) {
      this.update_error_message = true;
      return;
    }
  
    this.adminControlService.updateProductFromDB(this.actual_product)
    this.productOnClicked = false
  }

  insert() {
    if (!this.new_product.product_name || !this.new_product.category_key || !this.new_product.price || !this.new_product.image) {
      this.insert_error_message = true;
      return;
    }
    this.insert_error_message = false;

    this.adminControlService.addProductToDB(this.new_product)
    this.insertionOnClicked = false
    this.initNewProduct()
  }

  initNewProduct() {
    this.new_product = { "product_name": "", "category_key": null, "price": null, "image": "" }
  }

}
