import { Component, OnInit } from '@angular/core';
import { OrdersActionsService } from 'src/app/services/orders-actions/orders-actions.service';
import { Router } from "@angular/router"

@Component({
  selector: 'app-order-page',
  templateUrl: './order-page.component.html',
  styleUrls: ['./order-page.component.css']
})
export class OrderPageComponent implements OnInit {

  constructor(public orderActionsService: OrdersActionsService, public router: Router) { }

  ngOnInit(): void {
  }


  newPurchase(){
    this.orderActionsService.order_complete = false
    this.router.navigate(['/**'])
  }

}
