import { Component, OnInit , ViewChild } from '@angular/core';
import { AuthUserService } from 'src/app/services/auth_user/auth-user.service';
import {ProductsStoreService} from "../../../services/products-store/products-store.service"
import { MatSidenav } from '@angular/material/sidenav';

@Component({
  selector: 'app-shopping-page',
  templateUrl: './shopping-page.component.html',
  styleUrls: ['./shopping-page.component.css']
})
export class ShoppingPageComponent implements OnInit {

  constructor(private productStoreService:ProductsStoreService, private authUserService: AuthUserService) { }

  ngOnInit(): void {
    const userData = this.authUserService.getUserDetails();
    if (Object.entries(userData).length === 0) {
      this.authUserService.userConnected = false;
      return;
    }
    this.authUserService.userConnected = true;
  }

}
