import { Component, OnInit } from '@angular/core';
import { AuthUserService } from 'src/app/services/auth_user/auth-user.service';
import { CartActionsService } from 'src/app/services/cart-actions/cart-actions.service';
import { Router } from "@angular/router"
import { OrdersActionsService } from 'src/app/services/orders-actions/orders-actions.service';


@Component({
  selector: 'app-final-cart',
  templateUrl: './final-cart.component.html',
  styleUrls: ['./final-cart.component.css']
})
export class FinalCartComponent implements OnInit {

  public customer_cart_id: any
  public final_cart: any
  public displayed_cart: any
  public baseImagePath = "../../../assets/images/"
  public total: any
  public searchKey: any
  public no_products: boolean = false
  public search_mode: boolean = false

  constructor(public cartActionService: CartActionsService, public authUserService: AuthUserService, public router: Router,
    public orderActionsService: OrdersActionsService) {
  }

  ngOnInit(): void {

    const userData = this.authUserService.getUserDetails();
    this.customer_cart_id = userData.customer_cart_id
    this.initData();
    if (Object.entries(userData).length === 0) {
      this.authUserService.userConnected = false;
      return;
    }
    this.authUserService.userConnected = true;
  }

  async initData() {
    this.final_cart = await this.cartActionService.getFinalCart(this.customer_cart_id)
    this.displayed_cart = this.final_cart
    this.calculateTotal()
  }

  calculateTotal() {
    this.total = 0;
    for (let i = 0; i < this.final_cart.length; i++)
      this.total += this.roundNumber(Number(this.final_cart[i].total_price))
    this.orderActionsService.total_price = this.total
  }

  searchInReceipt() {
    if (!this.searchKey) {
      this.displayed_cart = this.final_cart
      this.no_products = false
      this.search_mode = false
      return
    }

    this.displayed_cart = this.final_cart.filter((product: any) => {
      return product.product_name.toLowerCase().includes(this.searchKey.toLowerCase())
    })

    if (!this.displayed_cart.length)
      this.no_products = true
    else {
      this.no_products = false
      this.search_mode = true
    }

  }

  roundNumber(number: any) {
    return Math.round(number * 100) / 100;
  }

}
